import addremark from '@/components/userup/addremark'
import bindphone from '@/components/userup/bindphone'
import index from '@/components/userup/index'
import index1 from '@/components/systemmange/index1'

Vue.use(Router)

export default new Router({
  mode:"history",
  routes: [
    {
      path: '/index1',
      name: '',
      component: index1
    },
    {
      path: '/index',
      name: '',
      component: index
    },
    {
      path: '/systemmange',
      name: '',
      component: systemmange
    },
    {
      path: '/userup',
      name: '',
      component: userup
    },
    {
      path: '/grouplist',
      name: '',
      component: grouplist
    },
    {
      path: '/memberlist',
      name: '',
      component: memberlist
    },
    {
      path: '/datamange',
      name: '',
      component: datamange
    },
    {
      path: '/addgroup',
      name: '',
      component: addgroup
    },
    {
      path: '/addmember',
      name: '',
      component: addmember
    },
    {
      path: '/groupdetail',
      name: '',
      component: groupdetail
    },
    {
      path: '/detailaddmember',
      name: '',
      component: detailaddmember
    },
    {
      path: '/changegroupname',
      name: '',
      component: changegroupname
    },
    {
      path: '/changemember',
      name: '',
      component: changemember
    },
    {
      path: '/reportdetail',
      name: '',
      component: reportdetail
    },
    {
      path: '/manageresult',
      name: '',
      component: manageresult
    },
    {
      path: '/extendsresult',
      name: '',
      component: extendsresult
    },
    {
      path: '/remarkinfo',
      name: '',
      component: remarkinfo
    },
    {
      path: '/deletemember',
      name: '',
      component: deletemember
    },
    {
      path: '/datacount',
      name: '',
      component: datacount
    },
    {
      path: '/sharedetail',
      name: '',
      component: sharedetail
    },
    {
      path: '/userupdetail',
      name: '',
      component: userupdetail
    },
    {
      path: '/addremark',
      name: '',
      component: addremark
    },
    {
      path: '/changeuserup',
      name: '',
      component: changeuserup
    },
    {
      path: '/adduserup',
      name: '',
      component: adduserup
    },
    {
      path: '/bindphone',
      name: '',
      component: bindphone
    },
    {
      path: '/power',
      name: '',
      component: power
    },
  ]
})



